import { Component, h, Prop, Element, State, Method } from '@stencil/core';
import InputInterface from './inputInterface';
import flatpickr from 'flatpickr';
import { common } from '../../global/data/common';

@Component({
      tag: 'ks-input-date',
      styleUrl: 'flatpickr.css'
})
export class InputDate implements InputInterface {

    @Prop() name: string;
    @Prop() placeholder: string;
    @Prop() label: string;

    @Prop() required: boolean;

    @Element() root: HTMLElement;

    @State() valid: boolean = true;
    @State() validMessage: string = "";

	render() {

        const valid = this.valid ? "" : "uk-form-danger";
        const validText = this.valid ? "" : "uk-text-danger";

		return(
            <div class="uk-margin">
                {this.label ? 
                    <label class={"uk-form-label " + validText} style={{marginBottom: "3px", display: "block"}}>{this.label}</label>
                : null}

			    <input class={"uk-input " + valid} name={this.name} type="text" placeholder={this.placeholder} onChange={()=>this.Validate()}/>
            
                {!this.valid ? 
                    <p class="uk-text-danger" style={{marginTop: "5px"}}>{this.validMessage}</p>
                : null}
            </div>
		);
    }

    @Method()
    async IsValid() {
        return this.valid;
    }

    @Method()
    async Validate() {
        const strings = common.get("translations");
        const input = this.root.querySelector("input");

        let valid = true;
        let message = "";

        if(this.required) {
            if(input.value.length == 0) {
                valid = false;
                message = strings.inputFieldRequired;
            }
        }

        this.valid = valid;
        this.validMessage = message;

        return Promise.resolve();
    }

    componentDidLoad() {
        const dateElement = this.root.querySelector("input");
        flatpickr(dateElement, {
            minDate: "today"
        });
    }
}
